<template>
	<div class="feedback-container">
		<div class="feedback-header">
			<div class="feedback-zhong">
				<div class="feedback-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="feedback-ti">意见反馈</div>
				<div></div>
			</div>
		</div>
		<div class="feedback-content">
			<div class="feedback-biaoti">请填写联系方式(选填)</div>
			<div class="feedback-kuang">
				<div class="feedback-zi">
					<textarea v-model="phone"></textarea>
				</div>
			</div>
			<div class="feedback-biaoti">请填写意见反馈内容</div>
			<div class="feedback-kuang1">
				<div class="feedback-wenzi">
					<textarea v-model="content"></textarea>
				</div>
				<!--<div class="question-tianjia">
					<img src="../assets/btn_shangchuan.png">
				</div>-->
			</div>
			<!--<div class="feedback-flex">
				<div class="feedback-xuan"  @click="collect">
					<img src="../assets/btn_niming.png"  v-show="downIcon"/>
					<img src="../assets/btn_niming2.png" v-show="!downIcon"/>
				</div>
				<div class="feedback-tiwen">匿名提问</div>
			</div>-->
			<div class="feedback-tijiao" @click="crt_feedback()">提交</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
//				user_info: {},
				data: {},
				content: '',
				downIcon: false,
				phone:''
			}
		},
		methods: {
			collect() {
				//根据你的downIcon判断此时的排序是升序还是降序
				//...排序方式
				this.downIcon = !this.downIcon
			},
			back() {
				window.history.back();
			},
			crt_feedback() {
				var that = this;
				var params = {
//					userid: that.user_info.user_id,
					content: that.content,
					phone: that.phone,
				};
				that.$http.post('/help/crt_feedback', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.content='',
						that.phone='',
//						this.$toast.success('提交成功');
						that.$alert(res.meta.msg,'提示');
						//						that.special_list = res.data;
					}else{
						that.$alert('请输入内容','提示');
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
//			if(!!localStorage.getItem('user_info')) {
//				this.user_info = JSON.parse(localStorage.getItem('user_info'));
//			}
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.feedback-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.feedback-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.feedback-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.feedback-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.feedback-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
		font-weight: 550;
	}
	
	.feedback-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.feedback-biaoti {
		font-size: 1.4rem;
		color: #8b8b8b;
		padding: 1rem 1.5rem;
	}
	
	.feedback-kuang {
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 3rem;
		margin: 0 1.5rem;
	}
	
	.feedback-zi textarea {
		width: 100%;
		height: 3rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	
	.feedback-wenzi textarea {
		width: 100%;
		height: 22rem;
		padding: 0 1.5rem;
		background: #f2f2f2;
		border: none;
		padding-top: 0.5rem;
	}
	
	.feedback-kuang1 {
		border: 1px solid #f2f2f2;
		border-radius: 5px;
		background: #f2f2f2;
		height: 22rem;
		margin: 0 1.5rem;
	}
	
	/*.question-tianjia img {
		width: auto;
		height: 6rem;
		position: relative;
		top: -7rem;
		left: 0.5rem;
	}
	*/
	.feedback-flex {
		display: flex;
	}
	
	.feedback-xuan img {
		width: auto;
		height: 1.5rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.feedback-tiwen {
		font-size: 1.5rem;
		color: #000000;
		padding: 0.9rem 0 1.6rem 0.5rem;
	}
	
	.feedback-tijiao {
		font-size: 1.8rem;
		border: 1px solid #fd5997;
		background: #fd5997;
		text-align: center;
		margin: 1rem 1.5rem;
		border-radius: 20px;
		padding: 0.5rem 0;
		color: #FFFFFF;
		margin-top: 3rem;
	}
</style>


